import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import About from './Components/About/About';
import Blog from './Components/Blog/Blog';
import Contact from './Components/Contact/Contact';
import Home from './Components/Home/Home';
import Portfolio from './Components/Portfolio/Portofolio';
import Topbar from './Components/Topbar';
import Navbar from './Components/Navbar';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";





const App = () => {
  const [lang, setLang] = useState("en");
  return (
    <div className="main">
      <Router >
        <Topbar />
        <Navbar lang={lang} />
        <div id="content">
          <div className="statusBar">
            <button className={(lang === "en") ? "lang is-active" : "lang"} onClick={() => setLang("en")}>English</button>
            <button className={(lang === "nl") ? "lang is-active" : "lang"} onClick={() => setLang("nl")}>Nederlands</button>
          </div>
          <Switch>
            <Route path="/about">
              <About lang={lang} />
            </Route>
            <Route path="/portfolio">
              <Portfolio lang={lang} />
            </Route>
            <Route path="/blog">
              <Blog lang={lang} />
            </Route>
            <Route path="/contact">
              <Contact lang={lang} />
            </Route>
            <Route path="/">
              <Home lang={lang} />
            </Route>
          </Switch>
        </div>
      </Router>
    </div>

  )
}



ReactDOM.render(React.createElement(App), document.getElementById("root"));