import React from 'react';
import {
    NavLink
} from "react-router-dom";

const NavLinks = (props) => {
    return (
        <div className="navLinks">
            <NavLink exact activeClassName='is-active' className="navLink" to="/">HOME</NavLink>
            <NavLink activeClassName='is-active' className="navLink" to="/about">{props.lang == "en" ? "ABOUT" : "OVER MEZELF"}</NavLink>
            <NavLink activeClassName='is-active' className="navLink" to="/portfolio" >PORTFOLIO</NavLink>
            <NavLink activeClassName='is-active' className="navLink" to="/blog">BLOG</NavLink>
            <NavLink activeClassName='is-active' className="navLink" to="/contact">CONTACT</NavLink>
        </div>
    )
}

export default NavLinks;
