import React, { useState } from 'react';
import { useForm } from 'react-hook-form';

const Contact = (props) => {
    const [representsCompany, setRepresentation] = useState(false);
    const [formSent, sendForm] = useState(false);
    const { handleSubmit, register, errors } = useForm();
    const onSubmit = values => {
        // WHAT SHOULD HAPPEN WHEN FORM IS SUBMIT!
        sendForm(true);
        console.log(values);
    };

    function companySwitch() {
        const oppositeValue = !representsCompany;
        setRepresentation(oppositeValue);
        console.log(representsCompany);
        console.log(errors)
    }

    return (
        <div className="content contact">
            <form className="content-item contact-form" onSubmit={handleSubmit(onSubmit)}>
                <label htmlFor="email">Email</label>
                <input id="email" ref={register({ required: true, maxLength: 50, pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i })} className="contact-input" name="email"
                    type="email" placeholder="placeholder@leeway.com"></input>
                {(errors.email) ? <p className="error">A valid email is required.</p> : false}

                <label htmlFor="name">{(props.lang === "en") ? "Name" : "Naam"}</label>
                <input id="name" className="contact-input" ref={register({ required: true, minLength: 2, maxLength: 40, pattern: /^[A-Za-z]+$/i })}
                    name="name" placeholder="Edwardo El Examplare"></input>
                {(errors.name) ? <p className="error">"A form needs a name"- Jaqen H'ghar. Maximum 40 letters.</p> : false}

                <label htmlFor="company">{(props.lang === "en" ? "Company" : "Bedrijf")}</label>
                <label htmlFor="companyRepresent" className="contact-input">
                    <input type="checkbox" id="companyRepresented" ref={register} name="companyRepresented" value={representsCompany}
                        checked={representsCompany} onChange={companySwitch} ></input>
                    I represent a company</label>
                {(representsCompany) ? <input id="company" className="contact-input" ref={register({ required: true, maxLength: 40 })}
                    name="company" placeholder="Microsapf LTD."></input> : false}
                {(errors.company) ? <p className="error">A company name is required.</p> : false}

                <label htmlFor="message">{(props.lang === "en") ? "Message" : "Bericht"}</label>
                {/* Things to be included still
                - Checkbox if you request a CV
                 */}
                <textarea id="message" name="message" ref={register({ required: true, minLength: 50, maxLength: 3000 })}
                    placeholder={(props.lang == "en") ? "This isn't even my final form!" : "Ik wil je een bericht achterlaten. Hopelijk is het niet dat dat je wou zeggen. "}></textarea>
                {(errors.message) ? <p className="error">A message is required! Minimum 50 letters, maximum 3000 letters.</p> : false}
                <button type="submit" className={(formSent ? "buttn-success" : "buttn")}>Send message</button>
                {(errors.name || errors.email || errors.message) ? <p className="error">Filled information isn't valid. Fix the errors, or don't. I can't tell you what to do.</p> : false}
            </form>
        </div >
    )
}

export default Contact;