import React from 'react';




const BlogPost = (props) => {
    const date = new Date(props.date);
    let formatted_date = date.toLocaleDateString('en-US', { day: "numeric", month: "long", year: "numeric" });
    // .getDate() + "-" + (date.getMonth() + 1) + "-" + date.getFullYear()
    const author = "Reinaert";


    return (
        <article className="content-item blogPost">
            <h2>{props.title}</h2>

            <h4><i>written by {author} on {formatted_date}</i></h4>

            <p>{props.body}</p>
        </article>
    )
}

export default BlogPost;